                  <a
                    target="_blank"
                    href="https://bscscan.com/address/0x698E165F2897e4daC68671c4cDFf337bbC543767"
                  >
                    0x698E165F2897e4daC68671c4cDFf337bbC543767(可点击链接)
                  </a>
                </p>
              </div>
<!--              <p>2. 本服务每十张卡需要支付 0.002 bnb，不足十张按十张计算</p>-->
