<script>
import Web3 from "web3";
import workAbi from "../abi/work.json";
import playAbi from "../abi/play.json";

import PropsTable from "./PropsTable.vue";
import ClipboardJs from "clipboard";
import { Content, Button, Alert, Icon, Tooltip } from "view-design";

const workType = {
  fullTime: 1,
  partTime: 2,
};

export default {
  name: "gold",
  components: {
    PropsTable,
    Content,
    Button,
    Alert,
    Icon,
    Tooltip,
  },
  data() {
    return {
      total: 0,
      tokens: [],
      selectedTokens: [],
      loading: true,
      feeRate: 10,
      baseFee: 0.002,
      propsDataSource: [],
      account: "",
      copyStatus: 0,
    };
  },

  async created() {
    await this.initWeb3Account();
    this.initContract();
    await this.getGoldInfo();
  },

  mounted() {
    // window.getProperty = this.getProperty;
    const accountBtn = new ClipboardJs("#account");
    const copyBtn = new ClipboardJs("#copy-btn");

    const successCb = () => {
      this.copyStatus = true;
      setTimeout(() => {
        this.copyStatus = false;
      }, 3000);
    };

    copyBtn.on("success", successCb);
    accountBtn.on("success", successCb);
  },

  methods: {
    // 初始化 web3及账号
    async initWeb3Account() {
      if (window.ethereum) {
        this.provider = window.ethereum;
        try {
          window.ethereum.enable();
        } catch (error) {
          //   console.log("User denied account access");
        }
      } else if (window.web3) {
        this.provider = window.web3.currentProvider;
      } else {
        this.provider = new Web3.providers.HttpProvider(
          "http://127.0.0.1:7545"
        );
      }
      this.web3 = new Web3(this.provider);
      let accs = await this.web3.eth.getAccounts();
      console.log("acces: ", accs);
      // 当前钱包的账号
      this.account = accs[0];
    },

    //  初始化合约实例
    initContract() {
      // 全职工作合约
      this.work = new this.web3.eth.Contract(
        workAbi,
        "0x698E165F2897e4daC68671c4cDFf337bbC543767"
      );
      // 兼职工作合约
      this.partTimeWork = new this.web3.eth.Contract(
        workAbi,
        "0xe278BDF4541cc309b379F9A4E867F60fD6B7BC59"
      );
      // 获取token 属性合约
      this.playContract = new this.web3.eth.Contract(
        playAbi,
        "0x79961F74D1E53CA480e4dDf5675D5A6D7B1852e7"
      );
    },

    async getTokenIds() {
      let tokenCount = await this.partTimeWork.methods
        .balanceOf(this.account)
        .call();
      let tokenIds = [];
      for (let i = 0; i < tokenCount; i++) {
        let tokenId = await this.partTimeWork.methods
          .tokenOfOwnerByIndex(this.account, i)
          .call();
        tokenIds.push(tokenId);
      }
      return tokenIds;
    },
    async getGoldInfoByTokenId(tokenId, account) {
      // 获取单个 tokenId 的收益, tokenId 必须属于 account，否则无法获取到收益
      // account  如果是获取自己的账号的收益，使用 this.account， 获取其他钱包的收益需要传入参数
      let gold;
      try {
        gold = await this.partTimeWork.methods
          .getAwardInfo(tokenId)
          .call({ from: account });
      } catch (e) {
        try {
          gold = await this.work.methods
            .getAwardInfo(tokenId)
            .call({ from: account });
        } catch (e) {
          // 不能获取到收益
        }
      }
      return gold;
    },
    async getGoldInfo() {
      const gold = this.web3.utils.toBN(0);
      const partTimeTokenCount = await this.partTimeWork.methods
        .balanceOf(this.account)
        .call();
      const fullWorkTokenCount = await this.work.methods
        .balanceOf(this.account)
        .call();

      const propsDataSource = [];

      for (let i = 0; i < partTimeTokenCount; i++) {
        const tokenId = await this.partTimeWork.methods
          .tokenOfOwnerByIndex(this.account, i)
          .call();

        let igold = await this.partTimeWork.methods
          .getAwardInfo(tokenId)
          .call({ from: this.account });
        igold = this.web3.utils.toBN(igold);
        gold.iadd(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });

        const tokenProperty = await this.getProperty(tokenId);
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.workType = workType.partTime;

        propsDataSource.push(tokenProperty);
      }

      for (let i = 0; i < fullWorkTokenCount; i++) {
        const tokenId = await this.work.methods
          .tokenOfOwnerByIndex(this.account, i)
          .call();

        let igold = await this.work.methods
          .getAwardInfo(tokenId)
          .call({ from: this.account });
        igold = this.web3.utils.toBN(igold);
        gold.iadd(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });

        const tokenProperty = await this.getProperty(tokenId);
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.workType = workType.fullTime;

        propsDataSource.push(tokenProperty);
      }

      this.propsDataSource = propsDataSource;
      this.loading = false;

      this.total = this.web3.utils.fromWei(gold.toString(), "ether");
    },
    payFee(tokens, callback) {
      const fee = this.calculateFee(tokens);

      return this.web3.eth.sendTransaction(
        {
          from: this.account,
          to: "0x4d09c2578690c7D4285D616f3eB1F53128148738",
          value: this.web3.utils.toWei(fee),
        },
        callback
      );
    },
    calculateFee(tokens) {
      return `${Math.ceil(tokens.length / this.feeRate) * this.baseFee}`;
    },
    async getProperty(tokenId) {
      let career_map = {
        "0xC6dB06fF6e97a6Dc4304f7615CdD392a9cF13F44": "法师",
        "0x22F3E436dF132791140571FC985Eb17Ab1846494": "战士",
        "0xF31913a9C8EFE7cE7F08A1c08757C166b572a937": "游侠",
        "0xaF9A274c9668d68322B0dcD9043D79Cd1eBd41b3": "盗贼",
        "0x819E04ddE93600b224F65e3C9B51b1B4D9fBa3b5": "卡特琳娜",
      };
      const data = await this.playContract.methods
        .getPlayerInfoBySet(tokenId)
        .call();

      return {
        tokenId: tokenId,
        // 职业地址
        career_addr: data[1],
        // 职业
        career: career_map[data[1]],
        // 力量
        strength: data[0][0],
        // 敏捷
        agility: data[0][1],
        // 体质
        physique: data[0][2],
        // 意志
        volition: data[0][3],
        // 智力
        brains: data[0][4],
        // 精神
        charm: data[0][5],
      };
    },
    async getAward() {
      const selectedTokens = this.selectedTokens;
      if (!selectedTokens.length) {
        this.$message.error("至少选择一项后操作");
        return;
      }

      for (const token of selectedTokens) {
        // 兼职
        if (token.workType === workType.partTime) {
          this.partTimeWork.methods
              .getAward(token.tokenId)
              .send({ from: this.account });
        } else if (token.workType === workType.fullTime) {
          // 全职
          this.work.methods
              .getAward(token.tokenId)
              .send({ from: this.account });
        }
      }

    //   // 支付费用
    //   this.payFee(
    //     selectedTokens, // 收菜
    //     (error) => {
    //       if (error != undefined) {
    //         console.log("error: ", error);
    //         return;
    //       }
    //       for (const token of selectedTokens) {
    //         // 兼职
    //         if (token.workType === workType.partTime) {
    //           this.partTimeWork.methods
    //             .getAward(token.tokenId)
    //             .send({ from: this.account });
    //         } else if (token.workType === workType.fullTime) {
    //           // 全职
    //           this.work.methods
    //             .getAward(token.tokenId)
    //             .send({ from: this.account });
    //         }
    //       }
    //     }
    //   ).catch(() => {
    //     this.$message.error("支付失败");
    //   });
    },
    batchGetAward() {},
  },
};
</script>
